import { getGoodsAttr, createGoodsAttr, updateGoodsAttr } from "@/api/goods";
export default {
  name: "GoodsAttrDetail",
  props: {
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      ruleForm: {
        attr_name: null,
        order: 1
      },
      rules: {
        attr_name: [{
          required: true,
          message: "请输入商品属性名称",
          trigger: "blur"
        }]
      }
    };
  },
  created: function created() {
    if (this.isEdit) {
      var id = this.$route.params && this.$route.params.id;
      this.fetchData(id);
    }
  },
  methods: {
    fetchData: function fetchData(id) {
      var _this = this;

      getGoodsAttr(id).then(function (response) {
        _this.ruleForm = response.data;
        console.log(_this.ruleForm);
      }).catch(function (err) {
        console.log(err);
      });
    },
    submitForm: function submitForm(formName) {
      var _this2 = this;

      this.$refs[formName].validate(function (valid) {
        if (valid) {
          if (_this2.isEdit) {
            var id = _this2.$route.params && _this2.$route.params.id;
            _this2.ruleForm._method = "put";
            updateGoodsAttr(id, _this2.ruleForm).then(function (response) {
              console.log(response);

              if (response.status_code === 200) {
                _this2.$notify({
                  title: "成功",
                  message: response.message,
                  type: "success",
                  duration: 2000
                });

                _this2.$router.go(-1);
              }
            }).catch(function (err) {
              console.log(err);
            });
          } else {
            createGoodsAttr(_this2.ruleForm).then(function (response) {
              console.log(response);

              if (response.status_code === 200) {
                _this2.$notify({
                  title: "成功",
                  message: response.message,
                  type: "success",
                  duration: 2000
                });

                _this2.$router.go(-1);
              }
            }).catch(function (err) {
              console.log(err);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm: function resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};